<template>
    <div class="page">
      <h3></h3>
      <pre>
        tar
            -c: 
            -x
            -t
            -r
            -u

            

            -zgzip
            -jbz2
            -Zcompress
            -v
            -O

            -f

            -f: 

            # tar -cf all.tar *.jpg
            .jpgall.tar-c-f

            # tar -rf all.tar *.gif
            .gifall.tar-r

            # tar -uf all.tar logo.gif
            tarall.tarlogo.gif-u

            # tar -tf all.tar
            all.tar-t

            # tar -xf all.tar
            all.tar-x

            
            tar cvf jpg.tar *.jpg //jpgtar.jpg
            tar czf jpg.tar.gz *.jpg   //jpgjpg.targzipgzipjpg.tar.gz
            tar cjf jpg.tar.bz2 *.jpg //jpgjpg.tarbzip2bzip2jpg.tar.bz2
            tar cZf jpg.tar.Z *.jpg   //jpgjpg.tarcompressumcompressjpg.tar.Z
            rar a jpg.rar *.jpg //rarrar for Linux
            zip jpg.zip *.jpg //zipzip for linux

            
            tar xvf file.tar // tar
            tar -xzvf file.tar.gz //tar.gz
            tar -xjvf file.tar.bz2   // tar.bz2
            tar xZvf file.tar.Z   //tar.Z
            unrar e file.rar //rar
            unzip file.zip //zip

            
            1*.tar  tar xvf 
            2*.gz  gzip -dgunzip 
            3*.tar.gz*.tgz  tar xzf 
            4*.bz2  bzip2 -dbunzip2 
            5*.tar.bz2tar xjf 
            6*.Z  uncompress 
            7*.tar.Z tar xZf 
            8*.rar  unrar e
            9*.zip  unzip 
      </pre>
    </div>
</template>

<script>
export default {
  name: 'tar',
  data () {
    return {
      msg: 'this is tar'
    }
  }
}
</script>

<style scoped>

</style>



