<template>
    <div>
      <h3></h3>
      <pre>
        
        class Cat(object):
        def sayHello(self):
            print("halou-----1")
        class Bosi(Cat):
            def sayHello(self):
                print("halou-----2")
        bosi = Bosi()
        bosi.sayHello()
        >>> halou-----2
      </pre>
      <h3>super() </h3>
      <pre>
        super MRO

        MRO , 

        class FooParent(object):
            def __init__(self):
                self.parent = 'I\'m the parent.'
                print ('Parent')

        def bar(self,message):
            print ("%s from Parent" % message)

        class FooChild(FooParent):
            def __init__(self):
                # super(FooChild,self)  FooChild  FooParentB FooChild  FooParent 
                super(FooChild,self).__init__()
                print ('Child')

        def bar(self,message):
            super(FooChild, self).bar(message)
            print ('Child bar fuction')
            print (self.parent)

        if __name__ == '__main__':
            fooChild = FooChild()
            fooChild.bar('HelloWorld')

        >>> Parent
        >>> Child
        >>> HelloWorld from Parent
        >>> Child bar fuction
        >>> I'm the parent.
      </pre>
    </div>
</template>

<script>
export default {
  name: 'cover-parent-func',
  data () {
    return {
      msg: 'this is cover-parent-func'
    }
  }
}
</script>

<style scoped>

