<template>
    <div>
      <h3></h3>
      <h3></h3>
      <pre>
        f = open('test.txt', 'w')

        r     
        w     
        a     
        rb    
        wb    
        ab    
        r+    
        w+    
        a+    
        rb+   
        wb+   
        ab+   
      </pre>
      <h3></h3>
      <pre>
        f.close()
      </pre>
      <h3></h3>
      <pre>
        f = open('test.txt', 'w')
        f.write('hello world, i am here!')
        f.close()
      </pre>
      <h3></h3>
      <pre>
        f = open('test.txt', 'r')
        content = f.read(5) # 5
        content = f.read() # 
        print(content)
      </pre>
      <h3>demo</h3>
      <pre>
        f1 = open('test.txt', 'r')
        content = f.read() # 

        f2 = open('test1.txt', 'w')
        f2.write(content)
        f1.close()
        f2.close()
      </pre>
      <h3></h3>
      <pre>
        tell()
        f = open("test.txt", "r")
        str = f.read(3)
        position = f.tell()
        position
        >>> 3

        seek()
        f.seek(5,0) # 05
        f.seek(-3,2) # 0
      </pre>
      <h3></h3>
      <pre>
        import os
        os.rename(".txt", "-.txt")
      </pre>
      <h3></h3>
      <pre>
        import os
        os.remove(".txt")
      </pre>
      <h3></h3>
      <pre>
        import os
        os.mkdir("")
      </pre>
      <h3></h3>
      <pre>
        import os
        os.getcwd()
      </pre>
      <h3></h3>
      <pre>
        import os
        os.chdir("../")
      </pre>
      <h3></h3>
      <pre>
        import os
        os.listdir("./")
      </pre>
      <h3></h3>
      <pre>
        import os
        os.rmdir("")
      </pre>
    </div>
</template>

<script>
export default {
  name: 'file',
  data () {
    return {
      msg: 'this is file'
    }
  }
}
</script>

