<template>
    <div>
      <h3>List </h3>
      <h3></h3>
      <!--eslint-disable-->
      <pre>
        namesList = ['xiaoWang','xiaoZhang','xiaoHua']
        print(namesList[2])    print(nameList[-1])
        >>> 'xiaoHua'

        index, count
        indexcount

        >>> a = ['a', 'b', 'c', 'a', 'b']
        >>> a.index('a', 1, 3) # 
        Traceback (most recent call last):
          File "< stdin >", line 1, in < module >
        ValueError: 'a' is not in list
        >>> a.index('a', 1, 4)
        3
        >>> a.count('b')
        2
        >>> a.count('d')
        0

        in,truefalse
        not intruefalse

        #
        nameList = ['xiaoWang','xiaoZhang','xiaoHua']

        #
        findName = input(':')

        #
        if findName in nameList:
          print('')
        else:
          print('')

      </pre>
      <h3></h3>
      <pre>
        demo:
            namesList = ['xiaoWang','xiaoZhang','xiaoHua']
            length = len(namesList)
            i = 0
            while length < i:
                print(namesList[i])
                i+=1
        :
            xiaoWang
            xiaoZhang
            xiaoHua
      </pre>
      <h3></h3>
      <pre>
        append
          append
          >>> a = [1, 2]
          >>> b = [3, 4]
          >>> a.append(b)
          >>> a
          [1, 2, [3, 4]]
        extend
          extend
          >>> a.extend(b)
          >>> a
          [1, 2, [3, 4], 3, 4]
        insert
           insert(index, object) indexobject
           >>> a = [0, 1, 2]
           >>> a.insert(1, 3)
           >>> a
           [0, 3, 1, 2]


      </pre>
      <h3></h3>
      <pre>
        A = ['xiaoWang','xiaoZhang','xiaoHua']
        A[1] = 'hello'
        print(A)
        >>>['xiaoWang','hello','xiaoHua']
      </pre>
      <h3></h3>
      <pre>
        
          del
          pop
          remove
            movieName = ['','','','','','']
            del movieName[2]
            movieName = ['','','','','']
            movieName.pop()
            movieName = ['','','','']
            movieName.remove('')
            movieName = ['','','']
      </pre>
      <h3> </h3>
      <pre>
        sortlistreverse=True
        reverselist
        >>> a = [1, 4, 2, 3]
        >>> a
        [1, 4, 2, 3]
        >>> a.reverse()
        >>> a
        [3, 2, 4, 1]
        >>> a.sort()
        >>> a
        [1, 2, 3, 4]
        >>> a.sort(reverse=True)
        >>> a
        [4, 3, 2, 1]
      </pre>
      <!--eslint-enable-->
    </div>
</template>

<script>
export default {
  name: 'python-list',
  data () {
    return {
      msg: 'this is python-list'
    }
  }
}
</script>

