<template>
    <div class="page">
      <h3>demo</h3>
      <!--eslint-disable-->
      <pre>
        #!/usr/bin/python3
        #coding:utf-8

        import sys
        import getopt
        import os
        import psycopg2.extras
        import uuid

        def strBoth(string):
            string = "'" + str(string) + "'"
            return string

        def merge(s, n) :
            src = s
            name = n
            nameArray = name.split('/')
            srcArray = src.split('/')
            if(nameArray[0]!='.'):
                nameArray = ['.'] + nameArray

            if (srcArray[-1] == ''):
                srcArray = srcArray[:-1]
            src = '/'.join(srcArray)
            name = '/'.join(nameArray)
            out = '/'.join(nameArray[:-1])
            out = out + '/'
            big = nameArray[-1]
            try:
                os.makedirs(src)
                print('%s'%(src))
                os.rmdir(src)
                sys.exit(2)
            except FileExistsError:
                print('')
            try:
                if os.path.isfile(name):
                    print('%s'%(name))
                    sys.exit(2)
                else:
                    tar(src, name, big, out)
            except FileExistsError:
                print('%s %s '%(name, big))
                sys.exit(2)

        def tar (s, n, b, u) :
            psycopg2.extras.register_uuid()
            src = s
            name = n
            bigfile_name = b
            conn = psycopg2.connect(database="filedb", user="postgres", password="postgres", host="10.10.92.233",port="5432")
            cursor = conn.cursor(cursor_factory=psycopg2.extras.DictCursor)
            id = strBoth(uuid.uuid1())
            root_url = strBoth(u)
            sql = 'delete from file_info WHERE bigfile_name = %s' % (strBoth(bigfile_name))
            cursor.execute(sql)
            try:
                os.makedirs(u)
            except FileExistsError:
                print('')
            for fpathe,dirs,fs in os.walk(src):
                for f in fs:
                    id = strBoth(uuid.uuid1())
                    file_name = strBoth(f)
                    fsize = int(os.path.getsize(os.path.join(fpathe, f)))
                    url = os.path.join(fpathe,f)
                    file = open(os.path.join(fpathe,f),'rb')
                    img = file.read()
                    big_file = open(name,'ab')
                    start_position = int(big_file.tell())
                    big_file.write(img)
                    bigfile_size = int(big_file.tell())
                    big_file.close()
                    file.close()
                    bigfile_name = strBoth(b)
                    url = url[len(src)+1:]
                    url = strBoth(url)
                    sql = 'INSERT INTO file_info (id, file_name, file_size, bigfile_name, bigfile_size, start_position, url, root_url) VALUES (%s, %s, %d, %s, %d, %d, %s, %s);'%(id, file_name, fsize, bigfile_name , bigfile_size, start_position, url, root_url)
                    cursor.execute(sql)
            conn.commit()
            conn.close()


        def downloadOne(filename, start_position, urlBigFile, DBurl, file_size, output, bFName):
            outputdir = output.split('/')
            if outputdir[0]!= '.':
                output = './' + output
            if outputdir[-1] != '':
                output = output + '/'
            realUrl = output + filename
            if os.path.isfile(realUrl):
                print('%s' % (realUrl))
                sys.exit(2)
            try:
                realUrlArray = realUrl.split('/')
                makeurl = '/'.join(realUrlArray[:-1]) + '/'
                os.makedirs(makeurl)
            except FileExistsError:
                print('')
            f = open(output + filename, 'wb') #   
            bigFile = open(urlBigFile,'rb') # 
            bigFile.seek(start_position,0)
            img = bigFile.read(file_size)
            f.write(img)
            f.close()
            bigFile.close()
            cmds = 'eog %s%s'%(output,strBoth(filename))
            os.popen(cmds)
            print('')

        def download(filename, outputDir,bigFileName):
            fileN = filename
            output = outputDir
            urlBigFile = bigFileName
            urlBigFileArray = urlBigFile.split("/")
            bFName = urlBigFileArray[-1]
            conn = psycopg2.connect(database="filedb", user="postgres", password="postgres", host="10.10.92.233", port="5432")
            cursor = conn.cursor(cursor_factory=psycopg2.extras.DictCursor)
            sql = "select * from file_info WHERE url = \'%s\' AND bigfile_name = \'%s\';"%(filename,bFName)
            cursor.execute(sql)
            answer = cursor.fetchone()
            if answer == None :
                print('')
            elif answer != None :
                print(answer)
                file_size = answer[2]
                start_position = answer[5]
                DBurl = answer[7]
                print('urlBigFile=', urlBigFile)
                test = os.path.isfile(urlBigFile)
                if test == False:
                    print('%s ' % (urlBigFile))
                    sys.exit()
                downloadOne(fileN, start_position, urlBigFile, DBurl, file_size, output, bFName)
            conn.commit()
            conn.close()

        def lsDir (bName):
            bArray = bName.split('/')
            bigName = bArray[-1]
            conn = psycopg2.connect(database="filedb", user="postgres", password="postgres", host="10.10.92.233", port="5432")
            cursor = conn.cursor(cursor_factory=psycopg2.extras.DictCursor)
            sql = 'select url,file_size,start_position from file_info WHERE bigfile_name = \'%s\'' % (bigName)
            cursor.execute(sql)
            rows = cursor.fetchall()
            print(rows)
            conn.commit()
            conn.close()
            if rows == [] :
                print(',')
            for item in rows:
                print("fileSize= '%s', startPosition= '%s', fileName= '%s'" % ( item[1], item[2], item[0]))

        def consoleLog():
            print(
                '''

              psycopg2 ,python psycopg2  psycopg2-binary  pip install -U pip ; pip install psycopg2 psycopg2,./ python fileMergeSline.py  
            :         [ ]
            
            -h --help             []
            -t --tar              
            -c --cat              ,eog
            -l --ls               postgresqlfileName

            :
            -d --dist             []
            -s --src              
            -n --name             -t 
                              -c  
            -b --bigFile          

            example:

            

            [sudo] [python3] ./fileMergeSline.py -t -s '' -n ''

            

            [sudo] [python3] ./fileMergeSline.py -l -b ''


            eog

            [sudo] [python3] ./fileMergeSline.py -c -n  [-d ''] -b ''

                '''
            )
        def main(argv):
            try:  #   h m: m 
                opts, args = getopt.getopt(argv, "htcld:s:n:b:", ["help", "tar", "cat", "ls", "dist=", "src=", "name=", "bigFile="])
            except getopt.GetoptError:
                print('         --help  -h ')
                sys.exit(2)

            for opt, arg in opts:
                if opt in ("-h", "--help"):
                    consoleLog()
                    sys.exit()
                elif opt in ("-t", "--tar"):
                    src = None
                    name = None
                    for item in opts:
                        if item[0] in ("-s", "--src"):
                            src = item[1]
                        elif item[0] in ("-n", "--name"):
                            name = item[1]

                    if src == None:
                        print(' -s ; --src')
                        sys.exit()

                    if name == None:
                        print(' -n ; --name')
                        sys.exit()
                    merge(src, name)
                    print('!')
                    sys.exit()
                elif opt in ("-c", "--cat"):
                    outputDirectory = None
                    fileName = None
                    bigFileName = None
                    for item in opts:
                        if item[0] in ("-d", "--dist"):
                            outputDirectory = item[1]
                        elif item[0] in ("-n", "--name"):
                            fileName = item[1]
                            print('fileName=', fileName)
                        elif item[0] in ("-b", "--bigFile"):
                            bigFileName = item[1]

                    if outputDirectory == None:
                        outputDirectory = './'

                    if fileName == None:
                        print(' -n')
                        sys.exit()
                    if bigFileName == None:
                        print(' -b')
                        sys.exit()
                    print('outputDirectory=',outputDirectory,'fileName=',fileName,'bigFileName=',bigFileName)
                    download(fileName, outputDirectory, bigFileName)
                elif opt in ("-l", "--ls"):
                    bigFileName = None
                    for item in opts:
                        if item[0] in ("-b", "--bigFile"):
                            bigFileName = item[1]
                    if bigFileName == None:
                        print(' -b')
                        sys.exit()
                    lsDir(bigFileName)

        if __name__ == "__main__":
            main(sys.argv[1:])
      </pre>
      <!--eslint-enable-->
    </div>
</template>

<script>
export default {
  name: 'pythonScript'
}
</script>

<style scoped>

</style>



