<template>
    <div class="page">
      <h3>
        
      </h3>
      <pre>
        less

        less:  colors.less

        @h1Font: #333; // 
        @h4Font: #666; // 
        @pFont:  #999; // 

        .style1{
          .change(#E1E2E8 , #76abd3 ,#4785b8, all 0.5s linear); /*  [0,1,6]  */
        }
        .style2{
          .change(#E1E2E8,  #b88bcb,#9f35ff, all 0.5s linear); /*  */
        }
        .style3{
          .change(#E1E2E8, #13cb89,#205527, all 0.5s linear); /*  */
        }
        .style0{
          .change(#999, #333,#000, all 0.5s linear); /*  */
        }

        

        @import "../../common/css/colors.less";
        .change(@background, @shallow, @deep, @transition) {
          .lookup {
            background: @background;
            transition: @transition;
          }
        }

        

        export default {
          name: 'm-header',
          methods: {
            changeStyle (num) {
              document.body.className = 'style' + num
              window.localStorage.setItem('skin', num)
            }
          },
          created () {
            let num = window.localStorage.getItem('skin')
            if (num) {
              document.body.className = 'style' + num
            } else {
              document.body.className = 'style1'
            }
          }
        }
      </pre>
    </div>
</template>

<script>
export default {
  name: 'skin'
}
</script>

<style scoped>

</style>



