<template>
    <div class="python-variable">
        <h3></h3>
        <pre>
           js

          
          import keyword
          print(keyword.kwlist)

          
        </pre>
        <h3></h3>
        <pre>
          a = 100

          def test1():
              print(a)

          >>> 100
        </pre>
        <h3></h3>
        <pre>
          a = 100 # 

          def test1():
              a = 200 # 
              print(a)

          >>> 200
        </pre>
    </div>
</template>

<script>
export default {
  name: 'python-variable',
  data () {
    return {
      msg: 'python-variable'
    }
  }
}
</script>

