          long(x [,base ])        x
          float(x )               x
          complex(real [,imag ])  
          str(x )                  x 
          repr(x )                 x 
          eval(str )              Python,
          tuple(s )                s 
          list(s )                 s 
          chr(x )                 
          unichr(x )              Unicode
          ord(x )                 
          hex(x )                 
          oct(x )                 
        </pre>
    </div>
</template>

<script>
export default {
  name: 'python-conversion',
  data () {
    return {
      msg: 'conversion'
    }
  }
}
</script>

