<template>
    <div>
      <h3>dict</h3>
      <pre>
        info = {'name':'', 'id':100, 'sex':'f', 'address':''}

        

        name = info.get('name')

        

        info['id'] = 99

        

        info['age'] = 88

        

        del info['name']  # 
        del info          # None
        info.clear()      # {}

         len()
        info = {'name':'', 'id':100, 'sex':'f', 'address':''}
        len(info)
        >>> 4

         dict.keys()
        info.keys()
        >>> ['name','id','sex','address']

         dict.values()
        info.values()
        >>> ['','100','f','']

        dict.items()
        info.items()
        >>> [('name':''), ('id':100), ('sex':'f'), ('address':'')]

        has_key
        info.has_key('name')
        True
        info.has_key('error')
        False
      </pre>
    </div>
</template>

<script>
export default {
  name: 'python-dictionary',
  data () {
    return {
      msg: 'this is python-dictionary'
    }
  }
}
</script>

<style scoped>

</style>



