<template>
    <div class="page">
      <h3></h3>
      <ul>
        <li>
          <h3>element-ui</h3>
          <pre>
            src
                main.js
              import ElementUI from 'element-ui'
              import 'element-ui/lib/theme-chalk/index.css'
              new Vue({
                el: '#app',
                router,
                store,
                echarts,
                components: { App },
                template: '&lt;App/&gt;'
              })
              Vue.use(ElementUI)
          </pre>
        </li>
        <li>
          <h3> axios</h3>
          <pre>
          src
            main.js
          import axios from 'axios'
          
              // 
                  axios.interceptors.request.use(function (config) {
                      // 
                      return config
                  }, function (error) {
                      // 
                      return Promise.reject(error)
                  })
              // 
                  axios.interceptors.response.use(function (response) {
                      // 
                      return response
                  }, function (error) {
                      // 
                       return Promise.reject(error)
                  })
          Vue.prototype.$http = axios
          </pre>
        </li>
        <li>
          <h3>echart</h3>
          <pre>
            src
              main.js
              import echarts from 'echarts'
              Vue.prototype.echarts = echarts

              new Vue({
                  el: '#app',
                  router,
                  store,
                  echarts,
                  components: { App },
                  template: '&lt;App/&gt;'
              })
          </pre>
        </li>
        <li>
          <h4>vuex</h4>
          <pre>
            src
              store # 
                  store.js # 
                  import Vue from 'vue'
                  import Vuex from 'vuex'
                  import mutations from './mutations'  #
                  Vue.use(Vuex)

                  export default new Vuex.Store({
                    state: {
                      count: 0
                    },
                    mutations
                })
            src
              store # 
                mutations.js # 

                const mutations = {
                    addition: state => state.count++,
                    minus: state => state.count--,
                    setZero: state => {
                      state.count = 0
                    }
                }
                export default mutations
          </pre>
        </li>
        <li>
          <h4>base64 ()</h4>
          <pre>
            src
              main.js
            Vue.prototype.Base64 = require('js-base64').Base64
            
            this.Base64.encode('');    // 5bCP6aO85by+
            this.Base64.encodeURI(''); // 5bCP6aO85by-

            this.Base64.decode('5bCP6aO85by+');  // 
            // note .decodeURI() is unnecessary since it accepts both flavors
            this.Base64.decode('5bCP6aO85by-');  // 
          </pre>
        </li>
        <li>
          <h4>less</h4>
          <pre>
            src
              webpack.base.conf.js
            {
                test: /\.less$/,
                loader: "style-loader!css-loader!less-loader"
             }
          </pre>
        </li>
        <li>
          <h4>()</h4>
          <pre>
            src
                router
                    index.js

            import Vue from 'vue'
            import Router from 'vue-router'
            import login from '@/components/login'
            Vue.use(Router)
            let routes = [
              {path: '/login',name: 'login',component: login}
            ]
            const router = new Router({
              history: true,
              routes: routes
            })
            router.beforeEach((to, from, next) => {
              if (to['redirectedFrom']) { // 
                let str = new RegExp(to['redirectedFrom'])
                if (str.test(from['fullPath']) === true) {
                  next(from['fullPath'])
                }
              }
              if (to.matched.length === 0) { // 
                from.name ? next({ name: from.name }) : next('/login')
              } else {
                next()
              }
            })
            export default router
          </pre>
        </li>
        <li>
          <h4>font-awesome</h4>
          <pre>
            src
              main.js
            import 'font-awesome/css/font-awesome.min.css'
          </pre>
        </li>
      </ul>
      <div class="bottomBar">
        <router-link to="/home/vue/package" class="left"><span class="fa fa-angle-double-left"></span>&nbsp;<span>npm</span></router-link>
        <router-link to="/home/vue/proxy" class="right"><span></span>&nbsp;<span class="fa fa-angle-double-right"></span></router-link>
      </div>
    </div>
</template>

<script>
export default {
  name: 'vue-use',
  data () {
    return {
      msg: 'this is vue-use'
    }
  }
}
</script>

