<template>
  <div>
    <h3></h3>
    <pre>
             Python                                                   
        +         [1, 2] + [3, 4]       [1, 2, 3, 4]                               
        *         'Hi!' * 4        ['Hi!', 'Hi!', 'Hi!', 'Hi!']                    
        in         3 in (1, 2, 3)           True                        
      not in      4 not in (1, 2, 3)        True                      
    </pre>
    <h3>id</h3>
    <pre>
      >>> a = 1
      >>> b = a
      >>> id(a)
      13033816
      >>> id(b)   # id
      13033816
      >>> a = 2
      >>> id(a)   # aid
      13033792
      >>> id(b)   # bid
      13033816
    </pre>
  </div>
</template>

<script>
export default {
  name: 'pythonOperator'
}
</script>

<style scoped>

</style>



