<template>
    <div>
      <h3></h3>
      <pre>
        1. Numbers()          - int()
                                  - long([])
                                  - float()
                                  - complex()

        2.()    - True
                                  - False

        3.String()          'hello'   "world"

        4.List()              []

        5.Tuple()             ()

        6.Dictionary()        {}
      </pre>
      <h3></h3>
      <pre>
                               
          +                  a + b  30
          -                  a - b  -10
          *                  a * b  200
          /                 xy b / a  2
          //             9//2  4 , 9.0//2.0  4.0
          %                b % a  0
          **                xy a**b 1020  100000000000000000000

          tips:
          9 // 2.0
          >>> 4
          9// 2.9
          >>>3
          2**3
          >>> 8
          9%2
          >>> 1
          a, b = 1, 2
          a
          >>> 1
          b
          >>> 2
      </pre>
    </div>
</template>

<script>
export default {
  name: 'python-type',
  data () {
    return {
      msg: 'this is python-type'
    }
  }
}
</script>

