<template>
    <div class="page">
        <h3>getopt </h3>
        <pre>
           try:  #   h m: m 
              opts, args = getopt.getopt(argv, "htcld:s:n:", ["help", "tar", "cat", "ls", "dist=", "src=", "name="])
           except getopt.GetoptError:
              print('         --help  -h ')
              sys.exit(2)

           for opt, arg in opts:
              if opt in ("-h", "--help"):
              showHelp()
              sys.exit()

              elif opt in ("-t", "--tar"):
                 src = None
                 name = None
                    for item in opts:
                       if item[0] in ("-s", "--src"):
                          src = item[1]
                       elif item[0] in ("-n", "--name"):
                          name = item[1]

                       if src == None:
                          print(' -s ; --src')
                          sys.exit()

                       if name == None:
                          print(' -n ; --name')
                          sys.exit()

                 merge(src, name)
                 print('!')
                 sys.exit()
        </pre>
    </div>
</template>
<script>
export default {
  name: 'python-getopt'
}
</script>

<style scoped>

</style>



