<template>
    <div>
      <!--eslint-disable-->
      <h3></h3>
      <h3></h3>
      <pre>
        # 
        class A:
            def printA(self):
                print('----A----')

        # 
        class B:
            def printB(self):
                print('----B----')

        # AB
        class C(A,B):
            def printC(self):
                print('----C----')

        obj_C = C()
        obj_C.printA()
        obj_C.printB()
        >>> ----A----
        >>> ----B----
      </pre>
      <h3></h3>
      <pre>
        class base(object):
            def test(self):
                print('----base test----')
        class A(base):
            def test(self):
                print('----A test----')

        # 
        class B(base):
            def test(self):
                print('----B test----')

        # AB
        class C(B,A): # 
            pass

        obj_C = C()
        obj_C.test()

        print(C.__mro__) #C
        >>>----B test----
        >>>(< class '__main__.C'>, < class '__main__.B'>, < class '__main__.A'>, < class '__main__.base'>, < class 'object'>)

         C B A base  
      </pre>
    </div>
</template>

<script>
export default {
  name: 'python-multi-inherit',
  data () {
    return {
      msg: 'this is python-multi-inherit'
    }
  }
}
</script>

<style scoped>

</style>



