<template>
    <div>
      <h3></h3>
      <pre>
        # :
        class Cat(object):

            def __init__(self, name, color=""):
                self.name = name
                self.color = color

            def run(self):
                print("%s--"%self.name)

        # Cat:
        class Bosi(Cat):

            def setNewName(self, newName):
                self.name = newName

            def eat(self):
                print("%s--"%self.name)

        bs = Bosi("")
        print('bs:%s'%bs.name)
        print('bs:%s'%bs.color)
        bs.eat()
        bs.setNewName('')
        bs.run()
        >>> bs
        >>> bs
        >>> 
        >>> 
      </pre>
      <h3></h3>
      <pre>
        ()
        
        
        
        
        
      </pre>
    </div>
</template>

<script>
export default {
  name: 'python-single-inheritance',
  data () {
    return {
      msg: 'this is python-single-inheritance'
    }
  }
}
</script>

<style scoped>

</style>



