<template>
    <div>
      <h3></h3>
      <pre>
        

        
      </pre>
      <h3></h3>
      <pre>
        1! = 1
        2! = 2  1 = 2  1!
        3! = 3  2  1 = 3  2!
        4! = 4  3  2  1 = 4  3!
        ...
        n! = n  (n-1)!

        def factorial(num):
          if num > 1:
            result = num * factorial(num - 1)
          else:
            result = 1
          return result
      </pre>
    </div>
</template>

<script>
export default {
  name: 'python-recursion',
  data () {
    return {
      msg: 'this is python-recursion'
    }
  }
}
</script>

<style scoped>

