<template>
    <div>
      <h3>__init__ </h3>
      <pre>
        # 
        class Car:

            def __init__(self, newWheelNum, newColor):
                self.wheelNum = newWheelNum
                self.color = newColor

            def move(self):
                print(':')

        # 
        BMW = Car(4, 'green')

        print(':%s'%BMW.color)
        print(':%d'%BMW.wheelNum)

        >>>:green
        >>>:4
      </pre>
      <h3>__init__ </h3>
      <pre>
        class People(object):

        def __init__(self, name):
            self.__name = name

        def getName(self):
            return self.__name

        def setName(self, newName):
            if len(newName) >= 5:
                self.__name = newName
            else:
                print("error:5")

        xiaoming = People("dongGe")

        xiaoming.setName("wanger")
        print(xiaoming.getName())
        >>> wanger

        xiaoming.setName("lisi")
        print(xiaoming.getName())
        >>> wanger # 5
        2'__'2
      </pre>
      <h3>__str__ </h3>
      <pre>
        class Car:

        def __init__(self, newWheelNum, newColor):
            self.wheelNum = newWheelNum
            self.color = newColor

        def __str__(self):
            msg = "" + self.color + ";" + int(self.wheelNum) + "..."
            return msg

        BMW = Car(4, "")
        print(BMW)

        >>>;4
      </pre>
      <h3>__del__ 0()</h3>
      <pre>
        class Animal(object):
          # 
          # 
          def __del__(self):
              print("%s..."%self.__name)

        # 
        dog = Animal("")

        # 
        del dog
        >>> ...

        cat = Animal("")
        cat2 = cat
        cat3 = cat

        print("--- cat")
        del cat
        print("--- cat2")
        del cat2
        print("--- cat3")
        del cat3
        >>> --- cat
        >>> --- cat2
        >>> --- cat3
        >>> ...
      </pre>
    </div>
</template>

<script>
export default {
  name: 'python-class-function',
  data () {
    return {
      msg: 'this is python-class-function'
    }
  }
}
</script>

<style scoped>

