<template>
    <div>
      <h3></h3>
      <pre>
        "%s,%d"%(name,age)
      </pre>
      <table>
        <tr>
          <td></td>
          <td></td>
        </tr>
        <tr>
          <td>%c</td>
          <td></td>
        </tr>
        <tr>
          <td>%s</td>
          <td>str() </td>
        </tr>
        <tr>
          <td>%i</td>
          <td></td>
        </tr>
        <tr>
          <td>%d</td>
          <td></td>
        </tr>
        <tr>
          <td>%u</td>
          <td></td>
        </tr>
        <tr>
          <td>%o</td>
          <td></td>
        </tr>
        <tr>
          <td>%x</td>
          <td></td>
        </tr>
        <tr>
          <td>%X</td>
          <td></td>
        </tr>
        <tr>
          <td>%e</td>
          <td>'e'</td>
        </tr>
        <tr>
          <td>%E</td>
          <td>E</td>
        </tr>
        <tr>
          <td>%f</td>
          <td></td>
        </tr>
        <tr>
          <td>%g</td>
          <td>fe </td>
        </tr>
        <tr>
          <td>%G</td>
          <td>fE</td>
        </tr>
      </table>
    </div>
</template>

<script>
export default {
  name: 'python-format'
}
</script>

<style scoped>

</style>



